main {
  background-image: url("svitok.1790c5a8.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  justify-content: center;
  align-items: center;
  display: flex;
}

.title {
  color: #19440b;
  font-size: 28px;
  font-weight: 700;
}

.box-form {
  min-height: 800px;
  max-width: 500px;
  margin: 0 auto;
  padding: 100px 0;
  font-family: Pacifico, sans-serif;
}

.form-input {
  color: #19440b;
  border-color: #19440b;
  border-radius: 10px;
  margin: 10px auto;
  padding: 15px;
  font-size: 24px;
  font-weight: 500;
}

.form-input:focus {
  outline: 2px #19440b;
}

::placeholder {
  color: #19440b;
  font-size: 24px;
  font-weight: 500;
}

.buy-list {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.buy-item {
  color: #19440b;
  justify-content: space-between;
  align-items: center;
  font-size: 24px;
  font-weight: 500;
  display: flex;
}

.clean-list {
  color: #19440b;
  letter-spacing: .5px;
  cursor: pointer;
  background-color: #14ff14b3;
  border: none;
  border-radius: 10px;
  margin: 10px auto;
  padding: 10px;
  box-shadow: 10px 10px 8px 2px #0000004d;
}

.buy-btn {
  color: #19440b;
  background-color: #14ff14b3;
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  font-size: 18px;
  font-weight: 400;
  box-shadow: 10px 10px 8px 2px #0000004d;
}

.buy {
  text-decoration: line-through;
  text-decoration-thickness: 5px;
}

